}

body {
  background-color: #ececec;
  font-size: 14px;
  font-weight: 400;
  font-family: "Nunito", 'Segoe UI', 'Open Sans', arial, Tahoma, Geneva, Verdana, sans-serif;
  color: #323232; }

.main-container {
  margin: 8vh;
  height: 84vh;
  overflow: hidden;
  border-radius: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  display: flex;
  flex-wrap: wrap; }

.left-container {
  flex-grow: 3;
  max-width: 70%;
  background-color: white;
  order: 1; }

.right-container {
  flex-grow: 1;
  max-width: 30%;
  background: #000;
  order: 2; }

.left-wrapper {
  position: relative;
  height: 84vh; }

nav {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  display: block;
  background-image: -webkit-gradient(linear, left bottom, left top, from(#ffffff), to(#d1d1d1));
  background-image: linear-gradient(to top, #ffffff, #d1d1d1);
  width: 100%;
  border-color: white;
  color: white; }

nav ul {
  display: flex;
  height: 8vh;
  margin: 0;
  padding: 0; }

nav ul a.active {
  background: white;
  font-weight: 800; }

nav ul a {
  cursor: pointer;
  color: #6f6f6f;
  flex-grow: 1;
  font-size: 17px;
  list-style-type: none;
  padding: 2vh 2%;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none; }

nav ul a i {
  visibility: hidden;
  display: none; }

nav ul a span {
  visibility: visible; }

.switch-wrapper {
  height: 76vh;
  background-image: -webkit-gradient(linear, left top, left bottom, from(white), to(#d8d8d8));
  background-image: linear-gradient(to bottom, white, #d8d8d8); }

.content {
  position: absolute;
  height: 68vh;
  max-height: 68vh;
  overflow: auto;
  background-image: -webkit-gradient(linear, left top, left bottom, from(white), to(#d8d8d8));
  background-image: linear-gradient(to bottom, white, #d8d8d8);
  padding: 4vh;
  left: 0;
  right: 0; }

.right-wrapper {
  position: relative;
  height: 84vh; }

.profile-name {
  position: absolute;
  color: white;
  width: 90%;
  margin: 5%;
  text-align: right;
  bottom: 5vh; }

.profile-name h1 {
  margin: 0; }

.profile-name p {
  margin: 0;
  color: #d1d3d4;
  font-weight: 700;
  border-radius: 5px; }

.nav-socialmedia {
  position: absolute;
  bottom: 0;
  width: 100%;
  display: block;
  background-image: -webkit-gradient(linear, left bottom, left top, from(#000000e0), to(transparent));
  background-image: linear-gradient(to top, #000000e0, transparent); }

.nav-socialmedia ul {
  display: flex;
  height: 7vh;
  margin: 0;
  padding: 0; }

.nav-socialmedia ul li {
  cursor: pointer;
  color: #ffffff;
  flex-grow: 1;
  font-size: 17px;
  list-style-type: none;
  padding: 2vh 2%;
  text-align: center;
  text-transform: uppercase; }

.nav-socialmedia ul li a {
  color: white; }

.nav-socialmedia ul li:hover {
  font-size: 22px; }

@media screen and (max-width: 768px) {
  .main-container {
    display: block;
    margin: 1vh;
    height: 98vh; }
  .left-container {
    max-width: none;
    width: 100%;
    height: 80vh; }
  .right-container {
    height: 25vh;
    max-width: none;
    overflow: hidden;
    border-radius: 5px; }
  .right-wrapper {
    height: 25vh; }
  .right-wrapper img {
    float: right;
    width: 30vh; }
  .profile-name {
    text-align: left; }
  .content {
    height: 57vh;
    max-height: 57vh; }
  .switch-wrapper {
    position: none;
    height: 65vh;
    max-height: 65vh; }
  nav {
    position: absolute;
    top: 65vh;
    background-image: -webkit-gradient(linear, left bottom, left top, from(white), to(#d8d8d8));
    background-image: linear-gradient(to top, white, #d8d8d8);
    box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.2); }
  nav ul a i {
    visibility: visible;
    display: block; }
  nav ul a span {
    visibility: hidden;
    display: none; } }

.indent {
  text-indent: 5%;
  text-align: justify; }

@media screen and (max-width: 768px) {
  .content h1 {
    font-size: 6vw; } }

.personal-title h1 {
  margin: 0; }

.personal-content {
  display: flex;
  margin-top: 2vh;
  font-size: 18px; }

.label {
  flex-shrink: 1;
  width: 50%;
  text-align: right;
  padding-right: 3%;
  font-weight: 700; }

.label i {
  padding-right: 10px; }

.data {
  flex-shrink: 1;
  width: 50%;
  padding-left: 3%; }

@media screen and (max-width: 768px) {
  .personal-content {
    font-size: 0.9em; }
  .label {
    width: 40%;
    text-align: right;
    padding-right: 1%;
    font-weight: 700; }
  .data {
    width: 60%; } }

.title-head {
  padding: 0;
  font-size: 1.6em;
  text-align: center;
  background-color: #c4c4c4;
  position: -webkit-sticky;
  position: sticky;
  padding: 2vh;
  top: 0; }

.title-head2 {
  padding: 0;
  font-size: 1.6em;
  text-align: center;
  background-color: #c4c4c4;
  position: -webkit-sticky;
  position: sticky;
  padding: 2vh;
  top: 0; }

/* ================ The Timeline ================ */
.timeline {
  position: relative;
  width: 660px;
  margin: 0 auto;
  margin-top: 20px;
  padding: 1em 0;
  list-style-type: none; }

.timeline:before {
  position: absolute;
  left: 50%;
  top: 0;
  content: ' ';
  display: block;
  width: 6px;
  height: 100%;
  margin-left: -3px;
  background: #505050;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(80, 80, 80, 0)), color-stop(8%, #505050), color-stop(92%, #505050), to(rgba(80, 80, 80, 0)));
  background: linear-gradient(to bottom, rgba(80, 80, 80, 0) 0%, #505050 8%, #505050 92%, rgba(80, 80, 80, 0) 100%);
  z-index: 5; }

.timeline li {
  padding: 1em 0; }

.timeline li:after {
  content: "";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden; }

.direction-l {
  position: relative;
  width: 300px;
  float: left;
  text-align: right; }

.direction-r {
  position: relative;
  width: 300px;
  float: right; }

.flag-wrapper {
  position: relative;
  display: inline-block;
  text-align: center; }

.flag {
  position: relative;
  display: inline;
  background: #f8f8f8;
  padding: 6px 10px;
  border-radius: 5px;
  font-weight: 600;
  text-align: left; }

.flag2 {
  background: #f8f8f8;
  padding: 6px 10px;
  border-radius: 5px;
  font-weight: 600;
  text-align: left; }

.direction-l .flag {
  box-shadow: -1px 1px 1px rgba(0, 0, 0, 0.15), 0 0 1px rgba(0, 0, 0, 0.15); }

.direction-r .flag {
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.15), 0 0 1px rgba(0, 0, 0, 0.15); }

.direction-l .flag:before,
.direction-r .flag:before {
  position: absolute;
  top: 50%;
  right: -40px;
  content: ' ';
  display: block;
  width: 12px;
  height: 12px;
  margin-top: -10px;
  background: #fff;
  border-radius: 10px;
  border: 4px solid #ff5050;
  z-index: 10; }

.direction-r .flag:before {
  left: -40px; }

.direction-l .flag:after {
  content: "";
  position: absolute;
  left: 100%;
  top: 50%;
  height: 0;
  width: 0;
  margin-top: -8px;
  border: solid transparent;
  border-left-color: #f8f8f8;
  border-width: 8px;
  pointer-events: none; }

.direction-r .flag:after {
  content: "";
  position: absolute;
  right: 100%;
  top: 50%;
  height: 0;
  width: 0;
  margin-top: -8px;
  border: solid transparent;
  border-right-color: #f8f8f8;
  border-width: 8px;
  pointer-events: none; }

.time-wrapper {
  display: inline;
  line-height: 1em;
  font-size: 0.66666em;
  color: #fa5050;
  vertical-align: middle; }

.direction-l .time-wrapper {
  float: left; }

.direction-r .time-wrapper {
  float: right; }

.time {
  display: inline-block;
  padding: 4px 6px;
  background: #f8f8f8; }

.desc {
  margin: 1em 0.75em 0 0;
  font-size: 0.77777em;
  font-style: italic;
  line-height: 1.5em;
  padding-top: 0; }

.desc h3 {
  margin-top: 20px;
  margin-bottom: 0px; }

.direction-r .desc {
  margin: 1em 0 0 0.75em; }

/* ================ Timeline Media Queries ================ */
@media screen and (max-width: 660px) {
  .timeline {
    width: 100%;
    padding: 2em 0 1em 0; }
  .timeline li {
    padding: 2em 0; }
  .direction-l,
  .direction-r {
    float: none;
    width: 100%;
    text-align: center; }
  .flag-wrapper {
    text-align: center; }
  .flag {
    background: white;
    z-index: 15; }
  .direction-l .flag:before,
  .direction-r .flag:before {
    position: absolute;
    top: -30px;
    left: 50%;
    content: ' ';
    display: block;
    width: 12px;
    height: 12px;
    margin-left: -9px;
    background: #fff;
    border-radius: 10px;
    border: 4px solid #ff5050;
    z-index: 10; }
  .direction-l .flag:after,
  .direction-r .flag:after {
    content: "";
    position: absolute;
    left: 50%;
    top: -8px;
    height: 0;
    width: 0;
    margin-left: -8px;
    border: solid transparent;
    border-bottom-color: white;
    border-width: 8px;
    pointer-events: none; }
  .time-wrapper {
    display: block;
    position: relative;
    margin: 4px 0 0 0;
    z-index: 14; }
  .direction-l .time-wrapper {
    float: none; }
  .direction-r .time-wrapper {
    float: none; }
  .desc {
    position: relative;
    margin: 1em 0 0 0;
    padding: 1em;
    background: whitesmoke;
    box-shadow: 0 0 1px rgba(0, 0, 0, 0.2);
    z-index: 15; }
  .direction-l .desc,
  .direction-r .desc {
    position: relative;
    margin: 1em 1em 0 1em;
    padding: 1em;
    z-index: 15; } }

@media screen and (max-width: 660px) {
  .direction-l .desc,
  .direction-r .desc {
    margin: 1em 4em 0 4em; } }

.portofolio-content {
